import styles from './Screen1.module.css';
import {Button} from "../../../universal/Button-click-show/Button";
import {useState} from "react";
import {logDOM} from "@testing-library/react";
import {useDispatch} from "react-redux";
import {setActiveSlide} from "../../../store/reducers/Swiper";

export const Screen1 = () => {
  const dispatch = useDispatch();

  const [buttons, setButtons] = useState(['Light', 'Premium', 'Premium Ultra']);

  const [activeBtn, setActiveBtn] = useState('');
  console.log(activeBtn)

  return (
    <div className={styles.container}>
      <p>Choose the type of cleaning:</p>
      <div className={styles.wrapper}>
        {buttons.map((item, index) => {
          return (
            <Button
              className={styles.button}
              show={true}
              title={item}
              active={activeBtn === item}
              callBack={setActiveBtn}
            />
          )
        })}
      </div>
      <button onClick={() => dispatch(setActiveSlide(1))}>   </button>
    </div>
  );
};