import styled from "styled-components";

export const Wrapper = styled.button`
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  ${({theme}) => {
  switch (theme) {
    case 'CRYSTAL':
      return `
        background: #d0b386;
        color: white;
        border-radius: 0;
      `;
    default: return null;
  };
}}
`;

export const Title = styled.p`
`;
