import {Title, Wrapper} from './ButtonStyled';
import styles from './Button.module.css';
import React, {useEffect, useState} from "react";

export const Button = ({
  title = 'button',
  theme = 'CRYSTAL',
  show = false,
  showDuration = 1,
  className,
  showStyles,
  callBack,
  active = false
}) => {

  const initialPositionShow = {x: 0, y: 0};
  const SHOW_BLOCK = React.createRef();
  const [positionShow, setPositionShow] = useState(initialPositionShow);
  const [classShow, setClassShow] = useState(false);

  const onShow = (e) => {
    const targetCoords = e.currentTarget.getBoundingClientRect();
    const x = e.clientX - targetCoords.left;
    const y = e.clientY - targetCoords.top;
    setPositionShow({x, y});
    setClassShow(false);
    setTimeout(() => setClassShow(true), 0);
  };

  const eventClick = (event) => {
    callBack && callBack(title);
    show && onShow(event);
  };

  const styledShowBlock = {
    borderRadius: '50%',
    animationDuration: showDuration + 's',
    background: 'white',
    opacity: 0,
    boxShadow: '0px 0px 20px 30px white',
    top: positionShow.y,
    left: positionShow.x,
    ...showStyles
  };

  const [rect, setRect] = useState(1120);
  const [transition, setTransition] = useState('stroke-dashoffset 2s');

  const stylesOutlineShow = {
    strokeDasharray: 1120,
    strokeWidth: '5px',
    fill: 'transparent',
    stroke: '#d0b386',
    borderBottom: '5px solid black',
    transitionTimingFunction: 'linear',
  };

  useEffect(() => {
    setRect(!active ? 1120 : 0)
    setTransition(active ? 'stroke-dashoffset 2s' : 'stroke-dashoffset 0.5s')
  }, [active])

  return (
    <div className={styles.container}>
      <div className={styles.stylesOutline}>
        <svg width='100%' height="100%"  xmlns="http://www.w3.org/2000/svg">
          <rect style={{...stylesOutlineShow, strokeDashoffset: rect, transition}} height="100%" width="100%"/>
        </svg>
      </div>
      <Wrapper
        theme={theme}
        onClick={eventClick}
        className={className}
      >
        <div
          ref={SHOW_BLOCK}
          style={styledShowBlock}
          className={`${styles.showBlock} ${classShow && styles.showBlockOn}`}
        ></div>
        <Title>{title}</Title>
      </Wrapper>
    </div>
  );
};
