import {setActiveSlide} from "../../../store/reducers/Swiper";
import {useDispatch} from "react-redux";

export const Screen2 = () => {

  const dispatch = useDispatch();
  return (
    <div >
      <button onClick={() => dispatch(setActiveSlide(0))}>  1 </button>
      <button onClick={() => dispatch(setActiveSlide(2))}>  3 </button>
      <p>screen 2</p>
    </div>
  )
}