import {useDispatch} from "react-redux";
import {setActiveSlide} from "../../../store/reducers/Swiper";

export const Screen3 = () => {
  const dispatch = useDispatch();
  return (
    <div >
      <button onClick={() => dispatch(setActiveSlide(1))}>  2 </button>
      <button onClick={() => dispatch(setActiveSlide(3))}>  4 </button>
      <p>screen 3</p>
    </div>
  )
}