import {setActiveSlide} from "../../../store/reducers/Swiper";
import {useDispatch} from "react-redux";

export const Screen4 = () => {

  const dispatch = useDispatch();
  return (
    <div>
      <button onClick={() => dispatch(setActiveSlide(2))}>  3 </button>
      <button></button>
      Screen4
    </div>
  )
}